import React from 'react';
import './checkout.css';
import {useStateValue} from './StateProvider';
import CheckoutProduct from './CheckoutProduct';
import Subtotal from './Subtotal';

const Checkout = () =>{
    const [{basket}] = useStateValue();
    return(
        <div className='checkout'>
        <div className="checkout__left">
            <img src="https://images-na.ssl-images-amazon.com/images/G/02/UK_CCMP/TM/OCC_Amazon1._CB423492668_.jpg"
            alt="" className='checkout__add' />
            
            {basket.length == 0 ? (
                <div>
                    <h2>Your Shopping Basket is empty</h2>
                    <p>You have no items in your basket.To buy one more items, click "Add to basket" next to the item.</p>
                </div>

            ) : (
                <div>
                    <h2 className="Shopping_Basket">Your Shopping Basket</h2>

                    {basket.map((items)=>{
                        console.log(items);
                        return(
                            <CheckoutProduct image={items.image}
                            price={items.price}
                            title={items.title} 
                            id={items.id}
                            />
                        )
                    })}
                    
                </div> 
            )
            }
        </div>

        {basket.length > 0 && (
            <div className="checkout__right">
                <Subtotal />
            </div>
        )}

     </div>
    );

}
export default Checkout;